
import { restAPIURL, xheader, ImMonkeyData_Users, ImMonkeyData_State } from '../constants/constants';


export const validatePin = function (pin: string, callback: Function): void {
    fetch(restAPIURL + '/authenticateuser', { method: 'POST', headers: xheader, body: JSON.stringify({ pin: pin }) })
        .then(res => res.json())
        .then((returnjson) => {
            callback(returnjson.success, returnjson.user);
        });
}

export const getAllMonkeyData = function (auth:boolean, callback: Function):void {
    fetch(restAPIURL + '/getmonkeydata', { method: 'POST', headers: xheader, body: JSON.stringify({}) })
        .then(res => res.json())
        .then((returnjson) => {
            returnjson.success
                ? callback(returnjson.data)
                : callback({})
        })
}

export const updateUser = function (userParams: ImMonkeyData_Users, callback: Function): void {
    fetch(restAPIURL + '/updateuser', { method: 'PUT', headers: xheader, body: JSON.stringify(userParams) })
        .then(res => res.json())
        .then((returnjson) => {
            returnjson.success
                ? callback(returnjson.user)
                : callback({})
        })
}

export const updateMonkeyData = function (userParams_state: Array<ImMonkeyData_State>, callback: Function): void {
    fetch(restAPIURL + '/updatemonkeydata', { method: 'PUT', headers: xheader, body: JSON.stringify(userParams_state) })
        .then(res => res.json())
        .then((returnjson) => {
            returnjson.success
                ? callback(true)
                : callback(false)
        })

}